import React, { useState } from 'react'
// import { Link } from 'react-router-dom'
import { Form, Button } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import Loader from '../components/Loader'
// import FormContainer from '../components/FormContainer'
// import { getUserDetails, updateUser } from '../actions/userActions'
// import { USER_UPDATE_RESET } from '../constants/userConstants'
import { editUser } from '../redux/actions/userActions'

const UserEditScreen = ({ username, email: userEmail, isAdmin: userIsAdmin, _id }) => {
  // const userId = match.params.id
  // console.log(userIsAdmin)
  const [name, setName] = useState(username)
  const [email, setEmail] = useState(userEmail)
  const [isAdmin, setIsAdmin] = useState(userIsAdmin)

  const dispatch = useDispatch()

  const userDetails = useSelector((state) => state.user)
  const { loading, error, } = userDetails

  const userUpdate = useSelector((state) => state.user)
  const {
    isLoading: loadingUpdate,
    isError: errorUpdate,
    // isSuccess: successUpdate,
  } = userUpdate

  // useEffect(() => {
  //   if (successUpdate) {
  //     dispatch({ type: USER_UPDATE_RESET })
  //     history.push('/admin/userlist')
  //   } else {
  //     if (!user.name || user._id !== userId) {
  //       dispatch(getUserDetails(userId))
  //     } else {
  //       setName(user.name)
  //       setEmail(user.email)
  //       setIsAdmin(user.isAdmin)
  //     }
  //   }
  // }, [dispatch, history, userId, user, successUpdate])

  const submitHandler = (e) => {
    e.preventDefault()
    const data = { username: name, email, isAdmin }
    dispatch(editUser({data, id: _id}))
  }

  return (
    <div style ={{ margin: '10px 40px 40px 40px',  height: '80vh'}}>
      {/* <Link to='/admin/userlist' className='btn btn-light my-3'>
        Go Back
      </Link> */}
      {/* <FormContainer > */}
        <h1>Edit User</h1>
        {loadingUpdate && <Loader />}
        {errorUpdate && <Message variant='danger'>{errorUpdate}</Message>}
        {loading ? (
          <Loader />
        ) : error ? (
          <Message variant='danger'>{error}</Message>
        ) : (
          <Form onSubmit={submitHandler}>
            <Form.Group controlId='name'>
              <Form.Label>Name</Form.Label>
              <Form.Control
                type='name'
                placeholder='Enter name'
                value={name}
                onChange={(e) => setName(e.target.value)}
              ></Form.Control>
            </Form.Group>

            <Form.Group controlId='email'>
              <Form.Label>Email Address</Form.Label>
              <Form.Control
                type='email'
                placeholder='Enter email'
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              ></Form.Control>
            </Form.Group>

            <Form.Group controlId='isadmin'>
              <Form.Check
                type='checkbox'
                label='Is Admin'
                checked={isAdmin}
                onChange={(e) => setIsAdmin(e.target.checked)}
              ></Form.Check>
            </Form.Group>

            <Button type='submit' variant='primary'>
              Update
            </Button>
          </Form>
        )}
      {/* </FormContainer> */}
    </div>
  )
}

export default UserEditScreen
